
import dayjs from "dayjs";
import { mapGetters, mapState } from "vuex";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import NoData from "@/components/noData";
import Scroll from "@/components/scrollAll";
import Query from "../../components/filter"
export default {
name: "costList",
props: {
cId: {
default: '0'
},
},
data() {
return {
costList: [],
params: {
customerId:0,
customerName:"",
page: 1,
count: 10,
createUserId:this.$store.state.CurrentUserID,
startCostDate:"",
endCostDate:"",
costType:0,
costClassId:0
},
pullUp: {
threshold: 0,
txt: {
more: "",
noMore: ""
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false,
noPt:false,
formatDate:"YYYY-MM-DD",
dateTiele:"",
filterArr: [
{
type: 0,
name: "",
init: 0,
notDict: true, //true
list: [
{
types: "",
ID: 0,
name: ""
},
{
types: "",
ID: 1,
name: ""
},
{
types: "",
ID: 2,
name: ""
}
]
},
{type:22,name:''}
],
};
},
methods: {
dateQuery(date) {
if(date.beginDate){
this.params.startCostDate = date.beginDate + " 00:00:00";
this.params.endCostDate = date.endDate + " 23:59:59";
}else{
this.params.startCostDate = ""
this.params.endCostDate = ""
}
this.getData(1, 1);
},
selectedParams(item){
if(item.types == ''){
this.params.costType = item.ID;
this.getData(1, 1);
this.$refs["scroll"].scrollTo(0, 0, 200)
}
if(item.DictType == 22){
this.params.costClassId = item.ID
this.getData(1, 1);
this.$refs["scroll"].scrollTo(0, 0, 200)
}
},
clearType() {
this.params.costType = 0;
this.params.costClassId = 0
this.getData(1, 1);
},
getData(type, page) {
let url1 = "/Cost/GetMyCostRecord"
if(this.cId!=0){
url1 = "/Cost/GetPagedCrmCostRecord"
}

this.$vux.loading.show({
text: ""
});
this.params.page = page || this.params.page;
if(this.cId){
this.params.customerId = this.cId;
}
this.getAxios(url1, this.params)
.then(res => {
this.$vux.loading.hide();
if (res.Data.List.length > 0) {
res.Data.List.forEach((item)=>{
item.CostDate = dayjs(item.CostDate).format('YYYY-MM-DD')
})
if (type === 2) {
this.costList = [...this.costList, ...res.Data.List];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.costList = res.Data.List;
this.$refs.scroll.scroll.finishPullDown();
}
this.params.page += 1;
this.$refs.scroll.refresh();
} else if (this.params.page == 1) {
this.costList = [];
this.$refs.scroll.forceUpdate();
} else {
this.$refs.scroll.forceUpdate();
}
})
.catch(err => {
this.$vux.loading.hide();
});
},
init(){
this.getData(1,1)
},
goto(path, id) {
this.$router.push({
path,
query: {id}
});
},
newCost(){
this.goto(`/cost/newCost`)
},
gotoInfo(item){
if(item.OrderNumber){
this.goto(`/cost/receiveInfo/${item.ReturnID}`)
}else{
if(this.cId !=0){
this.goto(`clientDetails/costInfo/${item.ID}`)
}else{
this.goto(`/cost/costInfo/${item.ID}`)
}
}
},
gotoIndex(){
this.goto("/")
},
selectedUser(item){





this.params.createUserId = item;
this.getData(1,1)
}
},
mounted() {
console.log(this.$route.params.perxObj)
let P = this.$route.params.perxObj
if(P){
if(P.createUserId){this.params.createUserId = P.createUserId}
if(P.startCostDate){this.params.startCostDate = P.startCostDate}
if(P.endDendCostDateate){this.params.endCostDate = P.endDendCostDateate}
if(P.costClassId||P.classID){this.params.costClassId = P.costClassId || P.classID}
if(P.type == ""){this.params.costType = 2}
}
this.getData()
this.$bus.on("refrechCostList", () => {
this.getData(1,1)
});
},
beforeDestroy() {
this.$bus.off("refrechCostList");
},
filters: {
getPicSrc(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
}
},
watch: {
costList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
}
},
beforeRouteLeave (to, from, next) {
if (to.path!='/newCost'&&to.path!='/costInfo') {
from.meta.keepAlive = false;
next();
}else{
next();
}
},
components: {
Flexbox,
FlexboxItem,
XHeader,
Scroll,
Group,
XInput,
NoData,
Query
}
};



