
import Scroll from '../scrollAll'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

import NoData from '../noData'
import Query from '../filter'

export default {
name: "receiveList",
components: {
Scroll,
Flexbox,
FlexboxItem,
XHeader,
Datetime,
Group,
NoData,
Query
},
props: {
cId: {
default: '0'
},
orderId: {
default: 0,
}
},
data() {
return {
clientList: [],
pageParams: {
orderId: this.orderId,
customerId: 0,
customerName: "",
beginReturnDate: "",
endReturnDate: "",
page: 1,
total: 10,
count: 10,
createUserId: 0
},
pullUp: {
threshold: 0,
txt: {
more: "",
noMore: ""
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false,
}
},
mounted() {
this.$nextTick(() => {







this.getData();
})
this.$bus.on('refreshReceiveList', () => {
this.pageParams.page = 1
this.getData()
})

if (this.$route.query.beginDate) {
this.pageParams.beginReturnDate = this.$route.query.beginDate
}else{
this.pageParams.beginReturnDate = ''
}
if (this.$route.query.endDate) {
this.pageParams.endReturnDate = this.$route.query.endDate
}else{
this.pageParams.endReturnDate = ''
}
if (this.$route.query.userId == -1) {
this.pageParams.createUserId = 0
}else{
this.pageParams.createUserId = ''
}
if (this.$route.query.userId == 0) {
this.pageParams.createUserId = this.$store.state.CurrentUserID
}else{
this.pageParams.createUserId = 0
}
},
beforeDestroy() {
this.$bus.off('refreshReceiveList')
},
methods: {
init() {


},
gotoAddPage() {
this.$router.push({
path: `/receive/addReceive?cid=${this.pageParams.customerId}`
})
},
getData(type) {
let _url = "/Cost/GetMyReturnedMoney";
if (this.cId != 0) {
_url = "/Cost/GetPagedCrmReturnedMoney";
}

if (type == 1) {
this.pageParams.page = 1;
}
this.pageParams.customerId = this.cId
this.Axios.get(_url, { params: this.pageParams }).then(res => {
let _list = res.data.Data.List;
if (_list.length > 0) {
if (type === 2) {
this.clientList = [
...this.clientList,
..._list
];
this.$nextTick(() => {
this.$refs.scroll.scroll.finishPullUp();
})
} else {
this.clientList = _list;
this.$nextTick(() => {
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.refresh(true);
})
}
this.pageParams.page += 1;
this.$nextTick(() => {
this.$refs.scroll.refresh();
})
} else {
this.clientList = _list
this.$nextTick(() => {
this.$refs.scroll.forceUpdate();
})
}
});
},
dateQuery(date) {
this.pageParams.beginReturnDate = date.beginDate
this.pageParams.endReturnDate = date.endDate
this.pageParams.page = 1
this.getData()
},
selectedUser(id) {
this.pageParams.createUserId = id
this.pageParams.page = 1
this.getData()
}
},
watch: {
clientList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
},
cId() {
this.$nextTick(() => {
this.$refs.checkList.style.paddingTop = '0'
})
this.getData()
},
orderId(newValue, oldValue) {
this.pageParams.orderId = newValue;
this.getData()
}
},
}



