import dayjs from "dayjs";
import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
export default {
  props: {
    cId: {
      default: 0
    }
  },
  data() {
    return {
      userList: [],
      params: {
        page: 1,
        count: 15,
        customerId: 0
      },
      pullUp: {
        threshold: 0,
        txt: {
          more: "",
          noMore: ""
        }
      },
      pullDown: {
        threshold: 50,
        stop: 20
      }
    };
  },
  mounted() {
    this.params.customerId = this.cId;
    this.getData();
    this.$bus.on("init", () => {
      this.init();
    });
  },
  methods: {
    dial(phone) {
      if (phone) {
        plus.device.dial(phone);
      } else {
        this.$vux.toast.show({
          type: "warn",
          text: ""
        });
      }
    },
    init() {
      this.getData(1, 1);
    },
    getData(type, page) {
      // 1  2 
      this.params.page = page || this.params.page;
      // setTimeout(() => {
      this.getAxios("/Customer/GetPagedCustomerLinkManList", this.params)
        .then(res => {
          if (res.Data.List.length > 0) {
            if (type === 2) {
              this.userList.push(...res.Data.List);
              this.$refs.scroll.scroll.finishPullUp();
            } else {
              this.userList = res.Data.List;
              this.$refs.scroll.scroll.finishPullDown();
              this.$refs.scroll.forceUpdate(false);
            }
            this.params.page += 1;
            this.$refs.scroll.refresh();
          } else {
            this.$refs.scroll.forceUpdate();
          }
        })
        .catch(err => {
          console.log(err);
        });
      // }, 1000);
    },
    goto(path) {
      this.$router.push({
        path
      });
    }
  },
  watch: {},
  filters: {
    dateFormat(val) {
      return val ? dayjs(val).format("YYYY-MM-DD") : "";
    }
  },
  components: {
    Scroll,
    NoData
  }
};



