import dayjs from "dayjs";
import { mapGetters, mapState } from "vuex";
import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
export default {
  props: {
    cId: {
      default: 0
    }
  },
  data() {
    return {
      clientList: [],
      params: {
        beginFollowupDate: "",
        endFollowupDate: "",
        page: 1,
        count: 10,
        customerId: 0
      },
      pullUp: {
        threshold: 0,
        txt: {
          more: "",
          noMore: ""
        }
      },
      pullDown: {
        threshold: 50,
        stop: 20
      },
      isSort: false
    };
  },
  methods: {
    init() {
      this.getData(1, 1);
    },
    getData(type, page) {
      this.$vux.loading.show({
        text: ""
      });
      this.params.page = page || this.params.page;
      this.getAxios("/Customer/GetPagedCustomerFollowup", this.params)
        .then(res => {
          this.$vux.loading.hide();
          if (res.Data.List.length > 0) {
            if (type === 2) {
              this.clientList = [...this.clientList, ...res.Data.List];
              this.$refs.scroll.scroll.finishPullUp();
            } else {
              this.clientList = res.Data.List;
              this.$refs.scroll.scroll.finishPullDown();
            }
            this.params.page += 1;
            this.$refs.scroll.refresh();
          } else if (page == 1) {
            this.clientList = [];
            this.$refs.scroll.forceUpdate(false);
          } else {
            this.$refs.scroll.forceUpdate(false);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    },
    goto(path, id) {
      this.$router.push({
        path,
        query: {
          id,
          isClient: true
        }
      });
    }
  },
  mounted() {
    if (this.cId) {
      this.params.customerId = this.cId;
      this.getData();
    }
    this.$bus.on("refrech", () => {
      this.getData(1, 1);
    });
  },
  beforeDestroy() {
    this.$bus.off("refrech");
  },
  watch: {
    clientList() {
      setTimeout(() => {
        this.$refs.scroll.forceUpdate(true);
      }, 500);
    },
    cId() {
      this.params.customerId = this.cId;
      this.getData();
    }
  },
  filters: {
    getPicSrc(id) {
      return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
    },
    dateFormat(date) {
      return dayjs(date).format("YYYY-MM-DD HH:mm");
    }
  },
  components: {
    Scroll,
    NoData
  }
};



