
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from "../scrollAll";
import NoData from "../noData";
import Query from "../filter";
import Progress from "@/components/progress";

export default {
name: "OrderList",
components: {
XHeader,
Scroll,
NoData,
Query,
Progress
},
props: {
cId: {
default: "0"
},
url: {
default: ""
}
},
data() {
return {
page: 1,
count: 20,
pullUp: {
threshold: 0,
txt: {
more: "",
noMore: ""
}
},
pullDown: {
threshold: 50,
stop: 20
},
list: [],
beginDealDate: "",
endDealDate: "",
createUserId: this.$store.state.CurrentUserID,
id: "",
expirEndDate: "",
orderStateData: '0'
};
},
mounted() {

if (this.$route.query.id) {
this.id = this.$route.query.id;
}
if (this.$route.query.enddate) {
this.expirEndDate = dayjs(new Date()).format("YYYY-MM-DD");
} else {
this.expirEndDate = "";
}

if (this.$route.query.beginDate) {
this.beginDealDate = this.$route.query.beginDate
} else {
this.beginDealDate = ''
}
if (this.$route.query.endDate) {
this.endDealDate = this.$route.query.endDate
} else {
this.endDealDate = ''
}
if (this.$route.query.userId && this.$route.query.userId == -1) {
this.createUserId = 0
}
if (this.$route.query.userId == 0) {
this.createUserId = this.$store.state.CurrentUserID
}

this.GetMyOrderList();
this.$bus.on("refreshOrderList",() => {
this.GetMyOrderList();
});
},
activated() {

},
beforeDestroy() {

},
methods: {

ratio(item) {
let moneyRatio =
item.TotalReturnMeony / item.TotalPrice != NaN
? (item.TotalReturnMeony / item.TotalPrice) * 100
: "";
if (moneyRatio == "100%" && item.TotalPrice - item.TotalReturnMeony > 0) {
moneyRatio = "99%";
}
return parseFloat(moneyRatio.toFixed(2));
},
init() {
this.GetMyOrderList();
},
getLastMoney(item) {
return (item.TotalPrice - item.TotalReturnMeony).toFixed(2);
},
GetMyOrderList() {
this.$vux.loading.show({
text: ""
});
let url = "";
if (this.$route.path == "/client/clientDetails") {
url = `/Order/GetPagedOrderList?page=${this.page}&count=${
this.count
}&customerId=${this.id}&createUserId=${
this.createUserId
}&beginDealDate=${this.beginDealDate}&endDealDate=${this.endDealDate}`;
} else {
url = `/Order/GetMyOrderList?page=${this.page}&count=${
this.count
}&customerId=${this.cId}&createUserId=${
this.createUserId
}&beginDealDate=${this.beginDealDate}&endDealDate=${
this.endDealDate
}&expirEndDate=${this.expirEndDate}&orderState=${this.orderStateData}`;
}
axios
.get(url)
.then(res => {
this.$vux.loading.hide();
if (res.data.State === 1) {
if (this.page === 1) {
this.list = res.data.Data.List;
this.$refs.scroll.scroll.finishPullDown();
} else {
this.list.push(...res.data.Data.List);
this.$refs.scroll.scroll.finishPullUp();
}
}











this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
})
.catch(e => {
this.$vux.loading.hide();
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
this.$vux.toast.show({
text: "",
type: "warn"
});
});
},
getData(status) {
if (status === 1) {
this.page = 1;
} else {
this.page++;
}
this.GetMyOrderList();
},
dateQuery(date) {
this.beginDealDate = date.beginDate;
this.endDealDate = date.endDate;
this.GetMyOrderList();
},
orderDate(type) {
if (type) {
this.expirEndDate = dayjs(new Date()).format("YYYY-MM-DD");
this.getData(1)
} else {
this.expirEndDate = ''
this.getData(1)
}
},
orderState(id) {
this.orderStateData = id
this.getData(1)
},
selectedUser(id) {
this.createUserId = id;
this.getData(1)
},
clearOrderType() {
this.orderStateData = '0'
this.getData(1)
},
enter() {
}
}
};



