import dayuan from "../../../common/js/dayuan.js";
import dayjs from "dayjs";
import scroll from "../../../components/scrollAll";
import noData from "../../../components/noData";

export default {
  data() {
    return {
      workList: [],
      page: 1,
      pullUp: {
        threshold: 0,
        txt: {
          more: "",
          noMore: ""
        }
      },
      pullDown: {
        threshold: 50,
        stop: 20
      }
    };
  },
  components: {
    scroll,
    noData
  },
  computed: {
    workParam() {
      return {
        customerId: this.cId,
        page: this.page,
        count: 10
      };
    },
    aImg() {
      let arr = this.workList.map(val => {
        return dayuan.aImg(val.CreateUserID);
      });
      return arr;
    },
    createTime() {
      let timeArr = this.workList.map(val =>
        dayjs(val.BeginDate).format("YYYY-MM-DD HH:mm")
      );

      return timeArr;
    },
    exeImg() {
      let exelist = this.workList.map(val => val.Executors);
      let arr = [];
      exelist.forEach((item, key) => {
        if (!arr[key]) {
          arr[key] = [];
        }
        item.forEach(items => {
          arr[key].push(dayuan.aImg(items.UserID));
        });
      });
      return arr;
    },
    extName() {
      let name = this.workList.map(val => val.Executors);
      return name;
    }
  },
  methods: {
    init() {
      this.getList(this.workParam);
    },
    getList(params) {
      this.$vux.loading.show({
        text: ""
      });
      this.Axios.get(`/CrmTask/GetPagedCrmTaskList`, { params }).then(res => {
        if (res.data.Data.List.length > 0) {
          if (this.page === 1) {
            this.workList = res.data.Data.List;
          } else {
            this.workList = [...res.data.Data.List, ...this.workList];
          }
        } else if (res.data.Data.List.length <= 0 && this.page === 1) {
          this.workList = [];
        }
        this.$refs.scroll.forceUpdate();
        setTimeout(() => {
          this.$vux.loading.hide();
        }, 500);
        // this.$bus.emit("onSave", true);
      });
    },
    listPulldown() {
      this.page = 1;
      this.getList(this.workParam);
      this.$refs.scroll.scroll.finishPullDown();
      this.$refs.scroll.refresh();
    },
    async listPullup() {
      this.page++;
      let pullBool = await this.getList(this.workParam);
      if (this.workList.length === 0) {
        return false;
      }
      this.$refs.scroll.scroll.finishPullUp();
      this.$refs.scroll.refresh();
    },
    toInfo(id) {
      this.$router.push({ path: "clientDetails/TaskInfo", query: { id } });
    }
  },
  mounted() {
    this.init();
    let hei = document.body.clientHeight;
    this.$refs.clientList.style.height = `${hei - 98}px`;
    // this.getList(this.workParam);
  },
  props: {
    cId: {}
  }
};



