import dayjs from "dayjs";
export default {
  props: {
    cId: {
      default: 0
    },
    item: {
      default: () => ({})
    }
  },
  data() {
    return {
      dataObj: {}
    };
  },
  mounted() {
    this.dataObj = this.item;
    // this.getData();
  },
  methods: {
    getData() {
      this.getAxios(`/Customer/GetCustomerInfoByID/${this.cId}`).then(res => {
        this.dataObj = res.Data;
      });
    },
    sourceFormat() {}
  },
  watch: {
    item() {
      this.dataObj = this.item;
    }
  },
  filters: {
    dateFormat(val) {
      return val ? dayjs(val).format("YYYY-MM-DD") : "";
    },
    sourceFormat(key) {
      let list = [
        {
          key: 1,
          value: ""
        },
        {
          key: 2,
          value: ""
        },
        {
          key: 3,
          value: ""
        },
        {
          key: 4,
          value: ""
        },
        {
          key: 5,
          value: ""
        },
        {
          key: 6,
          value: ""
        },
        {
          key: 7,
          value: ""
        },
        {
          key: 8,
          value: ""
        },
        {
          key: 9,
          value: ""
        },
        {
          key: 10,
          value: ""
        },
        {
          key: 11,
          value: ""
        }
      ];
      let obj;
      try {
        obj = list.find(item => item.key == key);
      } catch (err) {
        list.map(item => {
          if (item.key == key) {
            obj = item;
          }
        });
        //    obj = list.find(item => item.key == key);
      }
      return obj ? obj.value : "";
    }
  }
};



