
import { mapMutations, mapState } from "vuex";
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Follow from "@/components/follow/list";
import Details from "@/components/client/details";
import LinkUser from "@/components/client/userList";
import DelData from "@/components/del";
import OrderList from "@/components/order/orderList";
import CostList from "../cost/costList";
import WorkList from "../TaskWork/CurList/template";
import ReceiveList from "../../components/receive/receiveList";
import UserSelect from "@/components/User/template";
export default {
name: "clientDetails",
data() {
return {
custName: "",
tabType: 2,
tab: [
{
type: 1,
value: ""
},
{
type: 3,
value: "",
ref: "user"
},
{
type: 2,
value: "",
ref: "follow",
auth: ""
},
{
type: 4,
value: "",
ref: "orderlist",
auth: ""
},
{
type: 5,
value: "",
ref: "worklist",
auth: ""
},
{
type: 6,
value: "",
ref: "receiveList",
auth: ""
},
{
type: 7,
value: "",
ref: "costlist",
auth: ""
}
],
cId: 0,
isShow: false,
objData: {},
isCom: false,
userShow: false,
isError: false, //
isMsg: false,
userType: 0,
contentType: 0,
shareType: 0,
SaleChief: 0
};
},
mounted() {
this.$refs.details_body.style.height =
document.body.clientHeight - this.$refs.details_top.clientHeight + "px";
this.$bus.on("onSave", () => {
let ref = this.tab.find(item => item.type == this.tabType).ref;
if (ref) {
this.$refs[ref].init();
} else {
this.getData();
}
});
this.$bus.on("refrech", () => {
let ref = this.tab.find(item => item.type == this.tabType).ref;
if (ref) {
this.$refs[ref].init();
} else {
this.getData();
}
});
let query = this.$route.query;
this.cId = query.id;
this.SaleChief = query.SaleChief
this.isCom = query.isCom;
this.getData();

if (query.tab) {
this.$store.dispatch("initIsAdmin").then(res => {
let auth = this.tab.find(item => item.type == query.tab).auth;
if (!this.isAuth(auth) && query.tab != 3 && query.tab != 1) {
this.$vux.toast.show({
type: "warn",
text: ""
});
this.tabType = 1;
} else {
this.tabType = parseInt(query.tab);
}
});
} else {

if (query.backIndex && query.backIndex == 1) {
this.$store.dispatch("initIsAdmin").then(res => {
if (!this.isAuth("")) {
this.tabType = 1;
}
});
} else {
if (!this.isAuth("")) {
this.tabType = 1;
}
}
}
},
beforeDestroy() {
this.$bus.off("onSave");
this.$bus.off("refrech");
},
beforeRouteEnter(to, from, next) {
next(el => {
if (from.path == "/") {
if (window.plus) {
window.setTimeout(() => {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
}, 0);
} else {
document.addEventListener("plusready", () => {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
});
}
} else {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
el.$router.back();
});
}
});
},
methods: {
back() {
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
} else {
this.$router.back();
}
},

getClient() {
let _this = this;
this.$vux.confirm.show({
content: "",
onConfirm() {
let user = JSON.parse(localStorage.getItem("user")),
userID = user ? user.data.UserID : 204;
_this
.postAxios(
`/Customer/SetCustomerOwner?SaleChief=${userID}&type=1`,
[_this.cId]
)
.then(res => {
_this.$vux.toast.show({
type: "success",
text: ""
});
_this.$bus.emit("init");
_this.$router.back();
});
}
});
},


getData() {
this.getAxios(`/Customer/GetCustomerInfoByID/${this.cId}`,{userId: this.SaleChief})
.then(res => {
let data = res.Data;
this.objData = data;
this.cLink = data.LinkID;
this.custName = data.CustomerName;
this.cState = data.CustState;
})
.catch(res => {
if (res.ErrorMessage) {
this.isError = true;
this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
}
});
},
onItemClick(type) {
this.tabType = type;
},

toCommon() {
let _this = this,
user = JSON.parse(localStorage.getItem("user")),
userID = user ? user.data.UserID : 0;
if (this.objData.IsReport == 1) {
this.$vux.toast.show({
type: "cancel",
text: ""
});
return false;
}
this.$vux.confirm.show({
content: "",
onConfirm() {
_this
.postAxios(`/Customer/SetCustomerOwner?SaleChief=0&type=2`, [
_this.cId
])
.then(res => {
_this.$vux.toast.show({
type: "success",
text: ""
});
_this.$bus.emit("init");
_this.$router.back();
})
.catch(err => {
if (err.ErrorMessage) {
_this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
} else {
_this.$vux.toast.show({
type: "cancel",
text: ""
});
}
});
}
});
},
share(type, share) {
this.contentType = type;
this.shareType = share;
if (share == 2) {
this.passTo(1);
} else {
this.toShare();
}
},


toShare(data) {
let users = [];
if (data && data.length) {
data.map(item => {
users.push(item.ID);
});
}
let _this = this,
content = "";
switch (parseInt(this.contentType)) {
case 0:
content = "";
break;
case 1:
content = "";
break;
case 2:
content = "";
break;
default:
content = "";
}
this.$vux.confirm.show({
content: content,
onConfirm() {
let obj = {
IsShare: _this.shareType,
CustIds: [_this.cId],
UserIds: users
};
_this
.postAxios(`/Customer/SetCustomerIsShare`, obj)
.then(res => {
_this.$vux.toast.show({
type: "success",
text: ""
});
_this.$bus.emit("init");
_this.userShow = false;
_this.$router.back();
})
.catch(err => {
if (err.ErrorMessage) {
_this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
} else {
_this.$vux.toast.show({
type: "cancel",
text: ""
});
}
});
}
});
},

passTo(type) {

if (type === 2 && this.objData.IsReport == 1) {
this.$vux.toast.show({
type: "cancel",
text: ""
});
return false;
}
this.userType = type;
this.userShow = true;
},
selectedUser(data) {
if (this.userType == 1 && !data.length) {
this.$vux.toast.show({
type: "warn",
text: ""
});
return false;
}
if (this.userType == 2 && !data) {
this.$vux.toast.show({
type: "warn",
text: ""
});
return false;
}
if (this.userType == 1) {
this.toShare(data);
} else {
this.userSelected(data);
}
},

userSelected(data) {
let _this = this;
if (!data.ID) {
this.$vux.toast.show({
type: "warn",
text: ""
});
return false;
}
if (data.ID == this.CurrentUserID) {
this.$vux.toast.show({
type: "warn",
text: ""
});
return false;
}
this.$vux.confirm.show({
content: ``,
onConfirm() {
_this
.postAxios(
`/Customer/SetCustomerOwner?SaleChief=${data.ID}&type=3`,
[_this.cId]
)
.then(res => {
_this.$vux.toast.show({
type: "success",
text: ""
});
_this.send([data.ID], _this.cId);
_this.userShow = false;
_this.$bus.emit("init");
_this.$router.back();
})
.catch(err => {
if (err.ErrorMessage) {
_this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
} else {
_this.$vux.toast.show({
type: "cancel",
text: ""
});
}
});
}
});
},

send(userID, id) {
let obj = {
SendUsers: userID,
EvName: "crm-customer",
ImgPath: `${down}/mdtcrm/CRMClient.jpg`,
Title: "",
Content: "",
Params: [id]
};
this.postAxios(`${api50}/api/v1/Common/SendSingleEventMsg`, obj).then(
res => {
console.log(res);
}
);
},
del() {
let _this = this;
this.$vux.confirm.show({
content: "",
onConfirm() {
_this
.postAxios("/Customer/DeleteCustomerInfoByIds", [_this.cId])
.then(res => {
_this.$vux.toast.show({
type: "success",
text: ""
});
_this.$bus.emit("init");
_this.$router.back();
})
.catch(err => {
if (err.ErrorMessage) {
_this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
} else {
_this.$vux.toast.show({
type: "cancel",
text: ""
});
}
});
}
});
},
addTo() {
let path = "";
switch (this.tabType) {
case 2:
path = "addFollow";
break;
case 1:
path = "addClient";
break;
case 3:
path = "addCustUser";
break;
case 4:
path = "addOrder";
break;
case 5:
path = "addTask";
break;
case 6:
path = "addReceive";
break;
case 7:
path = "newCost";
break;
}
this.goto(`clientDetails/${path}`);
},
goto(path) {
this.$router.push({
path,
query: {
cId: this.cId,
cName: this.custName,
cLink: this.cLink,
cState: this.cState,
cCustStateName: this.objData.CustStateName,
cLinkName: this.objData.LinkName
}
});
},
isAuth(type) {


return this.auth.find(item => item.name == type);
},
isDel(val) {
return (
this.isAdmin ||
(val
? new Date().getTime() - new Date(val.replace(/-/g, "/")).getTime() <=
86400000
: "")
);
},

cancelReport() {
let _this = this;
this.$vux.confirm.show({
content: "",
onConfirm() {
_this
.getAxios("/Customer/CancelReport", {
customerId: _this.objData.ID
})
.then(res => {
_this.$vux.toast.show({
type: "success",
text: ""
});
_this.$bus.emit("init");
_this.$router.back();
})
.catch(err => {
if (err.ErrorMessage) {
_this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
} else {
_this.$vux.toast.show({
type: "cancel",
text: ""
});
}
});
}
});
}
},
computed: {
...mapState(["clientTab", "auth", "isAdmin", "CurrentUserID"]),
newTab() {
return this.tab.filter(item => !item.auth || this.isAuth(item.auth));
}
},
components: {
XHeader,
Tab,
TabItem,
Details,
LinkUser,
Follow,
OrderList,
CostList,
ReceiveList,
WorkList,
XButton,
UserSelect,
DelData
}
};



